import { API_BASE_URL, API_PREFIX } from '@/constants';

export interface ApiResponse<T = any> {
  data: T;
  message?: string;
  status: string;
}

export interface ApiError {
  message: string;
  status: number;
  errors?: Record<string, string[]>;
}

class ApiClient {
  private baseUrl: string;
  private token: string | null = null;

  constructor() {
    this.baseUrl = `${API_BASE_URL}${API_PREFIX}`;
  }

  setToken(token: string | null) {
    this.token = token;
  }

  private async request<T = any>(
    endpoint: string,
    options: RequestInit = {}
  ): Promise<T> {
    const url = `${this.baseUrl}${endpoint}`;
    
    const headers: HeadersInit = {
      'Content-Type': 'application/json',
      ...options.headers,
    };

    if (this.token) {
      headers.Authorization = `Bearer ${this.token}`;
    }

    const config: RequestInit = {
      ...options,
      headers,
    };

    try {
      console.log('API Request:', { url, method: config.method, headers }); // Debug log
      
      const response = await fetch(url, config);
      
      console.log('API Response status:', response.status); // Debug log
      
      if (!response.ok) {
        const error: ApiError = {
          message: response.statusText || 'An error occurred',
          status: response.status,
        };
        
        try {
          const errorData = await response.json();
          console.log('API Error data (raw):', errorData); // Debug log
          console.log('Error data type:', typeof errorData); // Debug log
          console.log('Error data keys:', Object.keys(errorData || {})); // Debug log
          
          // Handle different error formats:
          // 1. Documented format: { error, error_description, error_code }
          // 2. FastAPI default: { detail }
          // 3. Generic format: { message }
          error.message = errorData.error_description || errorData.detail || errorData.message || error.message;
          error.errors = errorData.errors || errorData.details;
          // Store additional error information
          (error as any).errorType = errorData.error || 'unknown_error';
          (error as any).errorCode = errorData.error_code;
          (error as any).retryAfter = errorData.retry_after;
        } catch (parseError) {
          // If we can't parse JSON, use the default message
          console.log('Failed to parse error JSON:', parseError);
          console.log('Response status:', response.status);
          console.log('Response statusText:', response.statusText);
        }
        
        throw error;
      }

      // Handle responses with no content (like 204 No Content)
      if (response.status === 204 || response.headers.get('content-length') === '0') {
        console.log('API Response: No content (204)'); // Debug log
        return null;
      }

      const data = await response.json();
      console.log('API Response data:', data); // Debug log
      return data;
    } catch (error) {
      console.log('API Request failed:', error); // Debug log
      
      if (error instanceof TypeError) {
        // Network error
        throw {
          message: 'Network error. Please check your connection.',
          status: 0,
        } as ApiError;
      }
      throw error;
    }
  }

  // Auth endpoints
  auth = {
    login: (credentials: { email: string; password: string; tenant_code: string }) => {
      // Use JSON format as per actual backend implementation
      const payload = {
        email: credentials.email,
        password: credentials.password,
        tenant_code: credentials.tenant_code,
      };
      
      return this.request('/auth/login', {
        method: 'POST',
        body: JSON.stringify(payload),
      });
    },

    refresh: (refreshToken: string) =>
      this.request('/auth/refresh', {
        method: 'POST',
        body: JSON.stringify({ refresh_token: refreshToken }),
      }),

    logout: () =>
      this.request('/auth/logout', {
        method: 'POST',
      }),

    validate: () => this.request('/auth/validate'),
  };

  // Users endpoints
  users = {
    me: () => this.request('/users/me'),

    list: (params?: { 
      page?: number; 
      limit?: number; 
      search?: string; 
      is_active?: boolean;
      sort?: string;
      order?: 'asc' | 'desc';
      tenant_id?: string;
    }) => {
      const searchParams = new URLSearchParams();
      if (params?.page) searchParams.set('page', params.page.toString());
      if (params?.limit) searchParams.set('limit', params.limit.toString());
      if (params?.search) searchParams.set('search', params.search);
      if (params?.is_active !== undefined) searchParams.set('is_active', params.is_active.toString());
      if (params?.sort) searchParams.set('sort', params.sort);
      if (params?.order) searchParams.set('order', params.order);
      if (params?.tenant_id) searchParams.set('tenant_id', params.tenant_id);
      
      const query = searchParams.toString();
      return this.request(`/users${query ? `?${query}` : ''}`);
    },

    get: (id: string) => this.request(`/users/${id}`),

    create: (userData: any) =>
      this.request('/users', {
        method: 'POST',
        body: JSON.stringify(userData),
      }),

    update: (id: string, userData: any) =>
      this.request(`/users/${id}`, {
        method: 'PATCH',
        body: JSON.stringify(userData),
      }),

    delete: (id: string, hard: boolean = false) =>
      this.request(`/users/${id}${hard ? '?hard_delete=true' : ''}`, {
        method: 'DELETE',
      }),

    updateAvatar: (id: string, avatarFile: File) => {
      const formData = new FormData();
      formData.append('avatar', avatarFile);
      
      return this.request(`/users/${id}/avatar`, {
        method: 'POST',
        body: formData,
        headers: {}, // Don't set Content-Type, let browser set it with boundary
      });
    },

    // Bulk operations
    bulk: (action: string, userIds: string[]) => {
      const endpoint = action === 'delete' ? '/users/bulk' : `/users/bulk/${action}`;
      return this.request(endpoint, {
        method: action === 'delete' ? 'DELETE' : 'POST',
        body: JSON.stringify({ user_ids: userIds }),
      });
    },

    // Admin password reset
    adminResetPassword: (id: string, newPassword: string) =>
      this.request(`/users/${id}/admin-reset-password`, {
        method: 'POST',
        body: JSON.stringify({ new_password: newPassword }),
      }),
  };

  // Tenants endpoints
  tenants = {
    list: (params?: {
      name?: string;
      code?: string;
      type?: 'root' | 'sub_tenant';
      parent_tenant_id?: string;
      is_active?: boolean;
      limit?: number;
      offset?: number;
    }) => {
      const searchParams = new URLSearchParams();
      if (params) {
        Object.entries(params).forEach(([key, value]) => {
          if (value !== undefined && value !== null) {
            searchParams.set(key, value.toString());
          }
        });
      }
      const query = searchParams.toString();
      return this.request(`/tenants${query ? `?${query}` : ''}`);
    },

    get: (id: string) => this.request(`/tenants/${id}`),

    getByCode: (code: string) => this.request(`/tenants/code/${code}`),

    create: (tenantData: {
      name: string;
      code: string;
      type?: 'root' | 'sub_tenant';
      parent_tenant_id?: string;
      isolation_mode?: 'shared' | 'dedicated';
      settings?: Record<string, any>;
      features?: string[];
      metadata?: Record<string, any>;
    }) =>
      this.request('/tenants', {
        method: 'POST',
        body: JSON.stringify(tenantData),
      }),

    update: (id: string, tenantData: {
      name?: string;
      settings?: Record<string, any>;
      features?: string[];
      metadata?: Record<string, any>;
      is_active?: boolean;
    }) =>
      this.request(`/tenants/${id}`, {
        method: 'PATCH',
        body: JSON.stringify(tenantData),
      }),

    delete: (id: string) =>
      this.request(`/tenants/${id}`, {
        method: 'DELETE',
      }),

    // Sub-tenant operations
    createSubTenant: (parentId: string, tenantData: {
      name: string;
      code: string;
      isolation_mode?: 'shared' | 'dedicated';
      settings?: Record<string, any>;
      features?: string[];
      metadata?: Record<string, any>;
    }) =>
      this.request(`/tenants/${parentId}/sub-tenants`, {
        method: 'POST',
        body: JSON.stringify(tenantData),
      }),

    // Hierarchy operations
    getHierarchy: (id: string) => this.request(`/tenants/${id}/hierarchy`),

    // Activation operations
    activate: (id: string) =>
      this.request(`/tenants/${id}/activate`, {
        method: 'POST',
      }),

    deactivate: (id: string) =>
      this.request(`/tenants/${id}/deactivate`, {
        method: 'POST',
      }),
  };

  // Roles endpoints
  roles = {
    list: (params?: {
      skip?: number;
      limit?: number;
      search?: string;
      is_active?: boolean;
      type?: 'system' | 'custom';
      sort_by?: string;
      sort_order?: 'asc' | 'desc';
    }) => {
      const searchParams = new URLSearchParams();
      if (params) {
        Object.entries(params).forEach(([key, value]) => {
          if (value !== undefined && value !== null) {
            searchParams.set(key, value.toString());
          }
        });
      }
      const query = searchParams.toString();
      return this.request(`/roles${query ? `?${query}` : ''}`);
    },

    get: (id: string) => this.request(`/roles/${id}`),

    create: (roleData: any) =>
      this.request('/roles', {
        method: 'POST',
        body: JSON.stringify(roleData),
      }),

    update: (id: string, roleData: any) =>
      this.request(`/roles/${id}`, {
        method: 'PATCH',
        body: JSON.stringify(roleData),
      }),

    delete: (id: string) =>
      this.request(`/roles/${id}`, {
        method: 'DELETE',
      }),

    assignToUser: (roleId: string, userId: string) =>
      this.request(`/roles/${roleId}/users/${userId}`, {
        method: 'POST',
      }),

    removeFromUser: (roleId: string, userId: string) =>
      this.request(`/roles/${roleId}/users/${userId}`, {
        method: 'DELETE',
      }),
  };

  // Service Accounts endpoints
  serviceAccounts = {
    list: () => this.request('/service-accounts'),

    get: (id: string) => this.request(`/service-accounts/${id}`),

    create: (serviceAccountData: any) =>
      this.request('/service-accounts', {
        method: 'POST',
        body: JSON.stringify(serviceAccountData),
      }),

    update: (id: string, serviceAccountData: any) =>
      this.request(`/service-accounts/${id}`, {
        method: 'PATCH',
        body: JSON.stringify(serviceAccountData),
      }),

    delete: (id: string) =>
      this.request(`/service-accounts/${id}`, {
        method: 'DELETE',
      }),

    regenerateSecret: (id: string) =>
      this.request(`/service-accounts/${id}/regenerate-secret`, {
        method: 'POST',
      }),
  };

  // Groups endpoints
  groups = {
    list: (params?: {
      is_active?: boolean;
      parent_group_id?: string;
      search?: string;
      skip?: number;
      limit?: number;
      sort_by?: string;
      sort_order?: 'asc' | 'desc';
    }) => {
      const searchParams = new URLSearchParams();
      if (params) {
        Object.entries(params).forEach(([key, value]) => {
          if (value !== undefined && value !== null) {
            searchParams.set(key, value.toString());
          }
        });
      }
      const query = searchParams.toString();
      return this.request(`/groups${query ? `?${query}` : ''}`);
    },

    get: (id: string) => this.request(`/groups/${id}`),

    create: (groupData: any) =>
      this.request('/groups', {
        method: 'POST',
        body: JSON.stringify(groupData),
      }),

    update: (id: string, groupData: any) =>
      this.request(`/groups/${id}`, {
        method: 'PATCH',
        body: JSON.stringify(groupData),
      }),

    delete: (id: string) =>
      this.request(`/groups/${id}`, {
        method: 'DELETE',
      }),

    // Member management
    addUsers: (groupId: string, userIds: string[]) =>
      this.request(`/groups/${groupId}/users`, {
        method: 'POST',
        body: JSON.stringify({ user_ids: userIds }),
      }),

    removeUser: (groupId: string, userId: string) =>
      this.request(`/groups/${groupId}/users/${userId}`, {
        method: 'DELETE',
      }),

    listMembers: (groupId: string) =>
      this.request(`/groups/${groupId}/users`),

    // Role management
    assignRoles: (groupId: string, roleIds: string[]) =>
      this.request(`/groups/${groupId}/roles`, {
        method: 'POST',
        body: JSON.stringify({ role_ids: roleIds }),
      }),

    removeRole: (groupId: string, roleId: string) =>
      this.request(`/groups/${groupId}/roles/${roleId}`, {
        method: 'DELETE',
      }),

    listRoles: (groupId: string) =>
      this.request(`/groups/${groupId}/roles`),
  };

  // Permissions endpoints
  permissions = {
    list: (params?: {
      resource_type?: string;
      is_active?: boolean;
      search?: string;
      skip?: number;
      limit?: number;
    }) => {
      const searchParams = new URLSearchParams();
      if (params) {
        Object.entries(params).forEach(([key, value]) => {
          if (value !== undefined && value !== null) {
            searchParams.set(key, value.toString());
          }
        });
      }
      const query = searchParams.toString();
      return this.request(`/permissions/${query ? `?${query}` : ''}`);
    },

    get: (id: string) => this.request(`/permissions/${id}`),

    create: (permissionData: any) =>
      this.request('/permissions/', {
        method: 'POST',
        body: JSON.stringify(permissionData),
      }),

    update: (id: string, permissionData: any) =>
      this.request(`/permissions/${id}`, {
        method: 'PATCH',
        body: JSON.stringify(permissionData),
      }),

    delete: (id: string) =>
      this.request(`/permissions/${id}`, {
        method: 'DELETE',
      }),

    // Role-permission management (these are under roles API)
    assignToRole: (roleId: string, assignments: any[]) =>
      this.request(`/roles/${roleId}/permissions/`, {
        method: 'POST',
        body: JSON.stringify({ permissions: assignments }),
      }),

    listByRole: (roleId: string, includeInherited: boolean = true) => {
      const params = new URLSearchParams();
      if (!includeInherited) params.set('include_inherited', 'false');
      const query = params.toString();
      return this.request(`/roles/${roleId}/permissions/${query ? `?${query}` : ''}`);
    },

    removeFromRole: (roleId: string, permissionId: string) =>
      this.request(`/roles/${roleId}/permissions/${permissionId}`, {
        method: 'DELETE',
      }),
  };

  // Resources endpoints
  resources = {
    list: (params?: {
      type?: string;
      parent_id?: string;
      is_active?: boolean;
      search?: string;
      page?: number;
      limit?: number;
      sort_by?: string;
      sort_order?: 'asc' | 'desc';
    }) => {
      const searchParams = new URLSearchParams();
      if (params) {
        Object.entries(params).forEach(([key, value]) => {
          if (value !== undefined && value !== null) {
            searchParams.set(key, value.toString());
          }
        });
      }
      const query = searchParams.toString();
      return this.request(`/resources${query ? `?${query}` : ''}`);
    },

    get: (id: string) => this.request(`/resources/${id}`),

    getTree: (params?: {
      root_id?: string;
      max_depth?: number;
    }) => {
      const searchParams = new URLSearchParams();
      if (params) {
        Object.entries(params).forEach(([key, value]) => {
          if (value !== undefined && value !== null) {
            searchParams.set(key, value.toString());
          }
        });
      }
      const query = searchParams.toString();
      return this.request(`/resources/tree${query ? `?${query}` : ''}`);
    },

    getStatistics: () => this.request('/resources/statistics'),

    create: (resourceData: any) =>
      this.request('/resources', {
        method: 'POST',
        body: JSON.stringify(resourceData),
      }),

    update: (id: string, resourceData: any) =>
      this.request(`/resources/${id}`, {
        method: 'PATCH',
        body: JSON.stringify(resourceData),
      }),

    delete: (id: string, cascade: boolean = false) =>
      this.request(`/resources/${id}${cascade ? '?cascade=true' : ''}`, {
        method: 'DELETE',
      }),

    move: (id: string, moveData: { new_parent_id: string | null }) =>
      this.request(`/resources/${id}/move`, {
        method: 'POST',
        body: JSON.stringify(moveData),
      }),

    getPermissions: (id: string) => this.request(`/resources/${id}/permissions`),

    getChildren: (id: string, params?: {
      is_active?: boolean;
      page?: number;
      limit?: number;
    }) => {
      const searchParams = new URLSearchParams();
      if (params) {
        Object.entries(params).forEach(([key, value]) => {
          if (value !== undefined && value !== null) {
            searchParams.set(key, value.toString());
          }
        });
      }
      const query = searchParams.toString();
      return this.request(`/resources/${id}/children${query ? `?${query}` : ''}`);
    },
  };

  // Terminology endpoints
  terminology = {
    get: (tenantId: string) => 
      this.request(`/terminology/tenants/${tenantId}`),

    update: (tenantId: string, data: {
      terminology: Record<string, string>;
      inherit_parent?: boolean;
      apply_to_children?: boolean;
      template_name?: string;
      metadata?: Record<string, any>;
    }) =>
      this.request(`/terminology/tenants/${tenantId}`, {
        method: 'PUT',
        body: JSON.stringify(data),
      }),

    reset: (tenantId: string) =>
      this.request(`/terminology/tenants/${tenantId}/reset`, {
        method: 'POST',
      }),

    applyToChildren: (tenantId: string, data: {
      terminology?: Record<string, string>;
      recursive?: boolean;
    }) =>
      this.request(`/terminology/tenants/${tenantId}/apply-to-children`, {
        method: 'POST',
        body: JSON.stringify(data),
      }),

    validate: (data: { terminology: Record<string, string> }) =>
      this.request('/terminology/validate', {
        method: 'POST',
        body: JSON.stringify(data),
      }),

    getDefaults: () =>
      this.request('/terminology/defaults'),

    getIndustryTemplates: () =>
      this.request('/terminology/templates'),

    applyTemplate: (tenantId: string, data: {
      template_name: string;
      customizations?: Record<string, string>;
    }) =>
      this.request(`/terminology/tenants/${tenantId}/apply-template`, {
        method: 'POST',
        body: JSON.stringify(data),
      }),
  };
}

export const apiClient = new ApiClient();