/**
 * Admin access control utilities for Sentinel RBAC system
 * Only users with admin permissions should be able to access the Sentinel UI
 */

/**
 * Admin permission scopes required for Sentinel UI access
 * Users need at least one of these permissions to access the system
 */
export const REQUIRED_ADMIN_SCOPES = [
  'user:admin',      // User management admin
  'tenant:admin',    // Tenant management admin  
  'role:admin',      // Role management admin
] as const;

/**
 * Check if user has any admin permissions required for Sentinel access
 * @param scopes - Array of user's permission scopes
 * @returns true if user has at least one admin permission
 */
export function hasAdminAccess(scopes: string[]): boolean {
  if (!scopes || scopes.length === 0) {
    return false;
  }

  // Check if user has any of the required admin scopes
  return REQUIRED_ADMIN_SCOPES.some(adminScope => scopes.includes(adminScope));
}

/**
 * Get the missing admin permissions for a user
 * @param scopes - Array of user's permission scopes  
 * @returns Array of missing admin scopes
 */
export function getMissingAdminScopes(scopes: string[]): string[] {
  if (!scopes || scopes.length === 0) {
    return [...REQUIRED_ADMIN_SCOPES];
  }

  return REQUIRED_ADMIN_SCOPES.filter(adminScope => !scopes.includes(adminScope));
}

/**
 * Get user's admin permissions from their scopes
 * @param scopes - Array of user's permission scopes
 * @returns Array of admin scopes the user has
 */
export function getUserAdminScopes(scopes: string[]): string[] {
  if (!scopes || scopes.length === 0) {
    return [];
  }

  return REQUIRED_ADMIN_SCOPES.filter(adminScope => scopes.includes(adminScope));
}