import { apiClient } from './api/client';
import type { TerminologyConfig, UpdateTerminologyRequest, TerminologyValidation } from '@/types';

/**
 * Frontend Terminology Service
 * 
 * Provides dynamic terminology mapping for Sentinel admin interfaces.
 * Fetches tenant-specific terminology from the backend and enables
 * real-time label replacement throughout the UI.
 * 
 * Features:
 * - Tenant-aware terminology loading
 * - In-memory caching with selective invalidation
 * - Fallback to default terminology
 * - Real-time terminology updates
 * - Industry template support
 */
export class TerminologyService {
  private cache = new Map<string, TerminologyConfig>();
  private listeners = new Set<(terminology: Record<string, string>) => void>();
  private currentTenantId: string | null = null;
  private currentTerminology: Record<string, string> = {};

  /**
   * Initialize the terminology service for a specific tenant
   */
  async initialize(tenantId: string): Promise<void> {
    this.currentTenantId = tenantId;
    
    try {
      const config = await this.getTerminology(tenantId);
      this.currentTerminology = config.terminology;
      this.notifyListeners(config.terminology);
    } catch (error) {
      console.warn('Failed to load terminology, using defaults:', error);
      this.currentTerminology = this.getDefaultTerminology();
      this.notifyListeners(this.currentTerminology);
    }
  }

  /**
   * Get terminology configuration for a tenant (with caching)
   */
  async getTerminology(tenantId: string): Promise<TerminologyConfig> {
    // Check cache first
    if (this.cache.has(tenantId)) {
      return this.cache.get(tenantId)!;
    }

    try {
      const config = await apiClient.terminology.get(tenantId);
      
      // Cache the result
      this.cache.set(tenantId, config);
      
      return config;
    } catch (error) {
      console.error('Failed to fetch terminology:', error);
      
      // Return default terminology as fallback
      const defaultConfig: TerminologyConfig = {
        terminology: this.getDefaultTerminology(),
        is_inherited: true,
        local_config: {},
        tenant_id: tenantId,
        tenant_name: 'Unknown',
        tenant_code: 'UNKNOWN',
      };
      
      return defaultConfig;
    }
  }

  /**
   * Update terminology configuration for a tenant
   */
  async updateTerminology(
    tenantId: string, 
    request: UpdateTerminologyRequest
  ): Promise<TerminologyConfig> {
    try {
      const updatedConfig = await apiClient.terminology.update(tenantId, request);
      
      // Update cache
      this.cache.set(tenantId, updatedConfig);
      
      // If this is the current tenant, update active terminology
      if (tenantId === this.currentTenantId) {
        this.currentTerminology = updatedConfig.terminology;
        this.notifyListeners(updatedConfig.terminology);
      }
      
      return updatedConfig;
    } catch (error) {
      console.error('Failed to update terminology:', error);
      throw error;
    }
  }

  /**
   * Reset tenant terminology to inherit from parent
   */
  async resetTerminology(tenantId: string): Promise<TerminologyConfig> {
    try {
      const resetConfig = await apiClient.terminology.reset(tenantId);
      
      // Update cache
      this.cache.set(tenantId, resetConfig);
      
      // If this is the current tenant, update active terminology
      if (tenantId === this.currentTenantId) {
        this.currentTerminology = resetConfig.terminology;
        this.notifyListeners(resetConfig.terminology);
      }
      
      return resetConfig;
    } catch (error) {
      console.error('Failed to reset terminology:', error);
      throw error;
    }
  }

  /**
   * Apply terminology to child tenants
   */
  async applyToChildren(
    tenantId: string,
    options: { terminology?: Record<string, string>; recursive?: boolean } = {}
  ): Promise<string[]> {
    try {
      const affectedTenants = await apiClient.terminology.applyToChildren(tenantId, options);
      
      // Invalidate cache for affected tenants
      if (Array.isArray(affectedTenants)) {
        affectedTenants.forEach(id => this.invalidateCache(id));
      }
      
      return affectedTenants;
    } catch (error) {
      console.error('Failed to apply terminology to children:', error);
      throw error;
    }
  }

  /**
   * Validate terminology configuration
   */
  async validateTerminology(terminology: Record<string, string>): Promise<TerminologyValidation> {
    try {
      return await apiClient.terminology.validate({ terminology });
    } catch (error) {
      console.error('Failed to validate terminology:', error);
      return {
        valid: false,
        errors: ['Failed to validate terminology'],
        warnings: [],
      };
    }
  }

  /**
   * Get industry templates
   */
  async getIndustryTemplates(): Promise<Record<string, Record<string, string>>> {
    try {
      return await apiClient.terminology.getIndustryTemplates();
    } catch (error) {
      console.error('Failed to fetch industry templates:', error);
      return {};
    }
  }

  /**
   * Apply an industry template to a tenant
   */
  async applyTemplate(
    tenantId: string,
    templateName: string,
    customizations?: Record<string, string>
  ): Promise<TerminologyConfig> {
    try {
      const config = await apiClient.terminology.applyTemplate(tenantId, {
        template_name: templateName,
        customizations,
      });
      
      // Update cache
      this.cache.set(tenantId, config);
      
      // If this is the current tenant, update active terminology
      if (tenantId === this.currentTenantId) {
        this.currentTerminology = config.terminology;
        this.notifyListeners(config.terminology);
      }
      
      return config;
    } catch (error) {
      console.error('Failed to apply template:', error);
      throw error;
    }
  }

  /**
   * Get the current active terminology (for the initialized tenant)
   */
  getCurrentTerminology(): Record<string, string> {
    return { ...this.currentTerminology };
  }

  /**
   * Translate a term using current terminology
   */
  translate(term: string): string {
    return this.currentTerminology[term.toLowerCase()] || term;
  }

  /**
   * Translate multiple terms at once
   */
  translateMultiple(terms: string[]): Record<string, string> {
    const result: Record<string, string> = {};
    terms.forEach(term => {
      result[term] = this.translate(term);
    });
    return result;
  }

  /**
   * Subscribe to terminology changes
   */
  subscribe(listener: (terminology: Record<string, string>) => void): () => void {
    this.listeners.add(listener);
    
    // Immediately call with current terminology
    listener(this.currentTerminology);
    
    // Return unsubscribe function
    return () => {
      this.listeners.delete(listener);
    };
  }

  /**
   * Invalidate cache for a specific tenant
   */
  invalidateCache(tenantId: string): void {
    this.cache.delete(tenantId);
  }

  /**
   * Clear all cached terminology
   */
  clearCache(): void {
    this.cache.clear();
  }

  /**
   * Get cache statistics (for debugging)
   */
  getCacheStats(): { size: number; keys: string[] } {
    return {
      size: this.cache.size,
      keys: Array.from(this.cache.keys()),
    };
  }

  /**
   * Get default Sentinel terminology
   */
  private getDefaultTerminology(): Record<string, string> {
    return {
      // Basic entities
      tenant: 'Tenant',
      sub_tenant: 'Sub-Tenant',
      user: 'User',
      role: 'Role',
      permission: 'Permission',
      resource: 'Resource',
      group: 'Group',

      // Plural forms
      tenants: 'Tenants',
      sub_tenants: 'Sub-Tenants',
      users: 'Users',
      roles: 'Roles',
      permissions: 'Permissions',
      resources: 'Resources',
      groups: 'Groups',

      // Actions
      create_tenant: 'Create Tenant',
      create_sub_tenant: 'Create Sub-Tenant',
      create_user: 'Create User',
      create_role: 'Create Role',
      create_group: 'Create Group',

      // Management pages
      tenant_management: 'Tenant Management',
      sub_tenant_management: 'Sub-Tenant Management',
      user_management: 'User Management',
      role_management: 'Role Management',
      permission_management: 'Permission Management',
      group_management: 'Group Management',

      // Dashboard and navigation
      dashboard: 'Dashboard',
      dashboard_title: 'Dashboard',
      welcome_message: 'Welcome back',
      admin_title: 'Administrator',

      // Common UI labels
      name: 'Name',
      type: 'Type',
      status: 'Status',
      actions: 'Actions',
      edit: 'Edit',
      delete: 'Delete',
      view: 'View',
      add: 'Add',
      manage: 'Manage',
      assign: 'Assign',
    };
  }

  /**
   * Notify all listeners of terminology changes
   */
  private notifyListeners(terminology: Record<string, string>): void {
    this.listeners.forEach(listener => {
      try {
        listener(terminology);
      } catch (error) {
        console.error('Error in terminology listener:', error);
      }
    });
  }
}

// Export singleton instance
export const terminologyService = new TerminologyService();

// Export default for convenience
export default terminologyService;