import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { apiClient, ApiError } from './client';
import { useAuthStore } from '@/store/auth';
import { User, Tenant, Role } from '@/types';

// Query keys
export const queryKeys = {
  auth: {
    me: ['auth', 'me'] as const,
  },
  users: {
    all: ['users'] as const,
    list: (params?: any) => ['users', 'list', params] as const,
    detail: (id: string) => ['users', 'detail', id] as const,
  },
  tenants: {
    all: ['tenants'] as const,
    list: () => ['tenants', 'list'] as const,
    detail: (id: string) => ['tenants', 'detail', id] as const,
  },
  roles: {
    all: ['roles'] as const,
    list: () => ['roles', 'list'] as const,
    detail: (id: string) => ['roles', 'detail', id] as const,
  },
  serviceAccounts: {
    all: ['service-accounts'] as const,
    list: () => ['service-accounts', 'list'] as const,
    detail: (id: string) => ['service-accounts', 'detail', id] as const,
  },
};

// Auth hooks
export const useAuth = () => {
  const { tokens, setTokens, logout } = useAuthStore();
  
  // Set token in API client when tokens change
  if (tokens?.access_token) {
    apiClient.setToken(tokens.access_token);
  }

  return {
    login: useMutation({
      mutationFn: apiClient.auth.login,
      onError: (error: ApiError) => {
        console.error('Login error:', error);
      },
    }),

    logout: useMutation({
      mutationFn: apiClient.auth.logout,
      onSettled: () => {
        logout();
        apiClient.setToken(null);
      },
    }),

    refresh: useMutation({
      mutationFn: apiClient.auth.refresh,
      onSuccess: (data) => {
        setTokens(data);
        apiClient.setToken(data.access_token);
      },
    }),

    me: useQuery({
      queryKey: queryKeys.auth.me,
      queryFn: apiClient.auth.me,
      enabled: !!tokens?.access_token,
      retry: false,
    }),
  };
};

// Users hooks
export const useUsers = (params?: { page?: number; limit?: number; search?: string }) => {
  return useQuery({
    queryKey: queryKeys.users.list(params),
    queryFn: () => apiClient.users.list(params),
  });
};

export const useUser = (id: string) => {
  return useQuery({
    queryKey: queryKeys.users.detail(id),
    queryFn: () => apiClient.users.get(id),
    enabled: !!id,
  });
};

export const useCreateUser = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: apiClient.users.create,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: queryKeys.users.all });
    },
  });
};

export const useUpdateUser = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: ({ id, data }: { id: string; data: any }) => 
      apiClient.users.update(id, data),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({ queryKey: queryKeys.users.all });
      queryClient.invalidateQueries({ 
        queryKey: queryKeys.users.detail(variables.id) 
      });
    },
  });
};

export const useDeleteUser = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: apiClient.users.delete,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: queryKeys.users.all });
    },
  });
};

export const useUpdateAvatar = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: ({ id, file }: { id: string; file: File }) => 
      apiClient.users.updateAvatar(id, file),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({ 
        queryKey: queryKeys.users.detail(variables.id) 
      });
      queryClient.invalidateQueries({ queryKey: queryKeys.auth.me });
    },
  });
};

